* {
    padding: 0;
    box-sizing: border-box;
    margin: 0;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    flex-direction: column;
}

.container {
    box-shadow: 0px 0px 12px #ccc;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 3rem;
}

input {
    width: 100%;
    padding: 15px 4px;
    font-size: 1.2rem;
    margin: 8px 0;
    border-radius: 8px;
}

.btn {
    display: block;
    width: 100%;
    font-size: 1.2rem;
    margin: 8px 0;
    padding: 15px 0;
    background-color: #0077EE;
    color: #fff;
    border: 1px solid #333;
    border-radius: 8px;
    cursor: pointer;
}

.btn-outline {
    background-color: #fff;
    color: #A6BCDA;
}

.center {
    text-align: center;
    margin: 24px 0;
}

p {
    margin: 10px 0;
}